<template>
	<div>
		<h1>Digimon Digital Adventures TV Homebrew Character Builder</h1>
		<p>{{msg}}</p>
	</div>
</template>

<script>
export default {
	name: 'DDA_Container',
	props: [],
	data: function () {
		return {
			msg: 'And so we begin...'
		}
	},
	computed: {},
	watch: {},
	methods: {},
	created: function () {},
	components: {},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
	font-weight: normal;
}
ul {
	list-style-type: none;
	padding: 0;
}
li {
	display: inline-block;
	margin: 0 10px;
}
a {
	color: #42b983;
}
</style>



